body {
  text-align: center;
}




.actor-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1em;
  background-color: #12282c;
}

h3 {
  font-size: 40px;
  margin-bottom: 10px;
}


img {
  width: 400px;
  height: 600px;
  border-radius: 10px;
  box-shadow: #20dbd8 0px 5px 15px; 
}
.movie-list-container {
  display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	grid-gap: 2em;
	max-width: 1200px;
	margin: auto;
	padding: 2em;
  background-color: #12282c;
}